<template>
	<div id="heart-recharge">
		<common-header :title="'爱心充值'"></common-header>
    <Cell :title="'充值方式'" @click="rechargeWay" :value="way"/>
    
    <div class="link-cell no-arrow">
	    <span>爱心数</span>
	    <input type="number" class="text-input needsclick" placeholder="想要获得的爱心数" v-model="heartNum">
	  </div>
	  
    <div class="btn" :class="{'active':isComplete}">充值</div>
    <div class="text">
    	充值后系统可能会延迟到账，若您长时间未到账，请与我们联系
    </div>
    
    <div class="shadow" v-if="showOrderType">
      <BottomPopup :itemList="itemList" @selItem="getBottomPopup"></BottomPopup>
    </div>
	</div>
</template>

<script>
import Cell from '@/components/Cell/Cell';
import BottomPopup from '@/components/Popup/BottomPopup'
	
export default {
	data(){
		return {
			way:'选择充值方式',
			heartNum:'',
      showOrderType: false,
			itemList:[
				{text:"微信充值",id:1},
				{text:"支付宝充值",id:2},
				{text:"钱包充值",id:3}
			],
		}
	},
	computed: {
		isComplete() {
			var isTrue = false;
			if(this.way!='选择充值方式' && this.heartNum > 0) {
				isTrue = true;
			}
			return isTrue;
		}
	},
  components: {
  	Cell,
  	BottomPopup
  },
  methods:{
  	rechargeWay() {
      this.showOrderType = true
    },
    getBottomPopup(res){
			if(res != 'cancel'){
				this.way = res.text
			}
			this.showOrderType = false
		},
  },
  
}
</script>

<style lang="scss">
	@import "../../../sass/baseSass.scss";
	#heart-recharge{
		height: 100%;
		background: #f0f0f0;
		.btn{
			font-size: 0.45rem;
			margin: 0.8rem auto 0.67rem;
			width: 9.2rem;
			height: 1.2rem;
			line-height: 1.2rem;
			border-radius: 0.6rem;
			text-align: center;
			background: #ccc;
			color: #fff;
		}
		.active{
			@include bg-color;
		}
		.text{
			padding: 0 0.4rem;
			font-size: 0.37rem;
			color: #666;
		}
		.link-cell {
	    position: relative;
	    display: flex;
	    flex-direction: row;
	    justify-content: space-between;
	    padding: 0 0.8rem 0 0.4rem;
	    height: 1.2rem;
	    line-height: 1.2rem;
	    background: #fff;
	    font-size: 0.427rem;
	    color: #333;
	
	    &:after {
	      content: '';
	      position: absolute;
	      top: 50%;
	      right: 0.4rem;
	      width: 0.173rem;
	      height: 0.307rem;
	      transform: translateY(-50%);
	      background-image: url('../../../assets/arrow_gray.png');
	      background-size: 100%;
	    }
	  }
	  .no-arrow {
	    padding-right: 0.4rem;

	    &:after {
	      content: none;
	    }
	  }
	}
</style>


